.header {
  background-color: #fff;
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
  box-shadow: 0 2px 4px #e8e7e7;
}

.logo {
  width: 40px;
  height: 40px;
  display: block;
}

.logo_image {
  width: 100%;
  height: 100%;
}

.nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav__item {
  margin-left: 20px;
}

.nav__item:first-child {
  margin-left: 0;
}

.nav__link {
  text-transform: uppercase;
  color: #060b35;
  align-items: center;
  height: 60px;
  font-size: 12px;
  font-weight: 500;
  text-decoration: none;
  transition: color .3s;
  display: flex;
  position: relative;
}

.nav__link:hover, .nav a.is-active, .nav__link.is-active {
  color: #00abcd;
}

.nav__link.is-active:after {
  content: " ";
  background-color: #00acdc;
  border-radius: 2px;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
}

body {
  --main-color: #060b35;
  --secondary-color: #616070;
  --button-color: #00acdc;
  margin: 0;
  font-family: Roboto, sans-serif;
}

.product {
  box-sizing: border-box;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  width: 200px;
  padding: 32px 16px 16px;
  transition-duration: .5s;
}

.product__image {
  width: 160px;
  height: 134px;
  margin: 0 auto 40px;
  display: block;
}

.product__name {
  color: var(--main-color);
  margin: 0 0 4px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.product__code {
  color: var(--secondary-color);
  margin-bottom: 16px;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.product__reviews {
  justify-content: space-between;
  align-items: center;
  margin-bottom: 24px;
  display: flex;
}

.product__reviewsQuantity {
  color: var(--main-color);
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.product__price {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.product__price--text {
  color: var(--secondary-color);
  font-size: 12px;
  line-height: 18px;
}

.product__price--value {
  color: var(--main-color);
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.product__botton {
  box-sizing: border-box;
  border: 1px solid var(--button-color);
  background-color: var(--button-color);
  color: #fff;
  text-transform: uppercase;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  height: 40px;
  margin-top: 16px;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  text-decoration: none;
  display: flex;
}

.product__botton:hover {
  color: var(--button-color);
  background-color: #fff;
}

.product:hover {
  transition-duration: .5s;
  transform: scale(1.2);
}

p {
  margin: 0;
  display: block;
}

.stars {
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  margin-right: 4px;
}

.stars--4 span:nth-child(-n+4) {
  background-image: url("star-active.edda4c43.svg");
}

.container {
  box-sizing: border-box;
  grid-template-areas: "product product product product"
                       "product product product product";
  justify-content: center;
  align-items: center;
  gap: 46px 48px;
  padding: 50px 40px;
  display: grid;
}
